<template>


<div class="container mt-3">

    <!-- Reagendar Pedido -->
    <b-modal v-model="modalShow"
            centered
            size="md"
            title="Reagendar pedido"
            :header-bg-variant="headerBgVariant"
            :header-text-variant="headerTextVariant">
        <b-container fluid class="container">
            <div class="col-12">
				<strong><label>Hora de salida de la planta</label></strong>
                <input v-model="pedidoReagendar.horaSalida"
                        class="form-control"
                        type="time"/>
            </div>

            <div class="col-12">
                <strong><label>Fecha de salida</label></strong>
				<b-form-datepicker v-model="pedidoReagendar.fechaSalida"
									size="sm"
									locale="es">
				</b-form-datepicker>
            </div>

			<div class="col-12">
                <strong><label>Motivo</label></strong>
				<textarea v-model="pedidoReagendar.motivo"
							class="form-control"
							rows="2"/>
            </div>

        </b-container>

        <template #modal-footer>
          <div class="w-100">

			<b-button @click="modalShow = false"
						variant="danger"
						class="float-right">
              Cancelar
            </b-button>
            <b-button @click="ReagendarPedido"
						variant="primary"
						class="float-right mr-2">
              Aceptar
            </b-button>

			
          </div>
        </template>

    </b-modal>

    <!-- Ver detalles -->
    <b-modal :id="infoModal.id"
            :title="infoModal.title"
            ok-only
            @hide="resetInfoModal"
            :codigo="infoModal.codigo"
            hide-footer>
      <tr v-for="(value, key) in infoModal.content" :key="key">
        <th>{{ key }}</th>
        <!-- Hay que hacerlo más dinámico -->
        <td v-if ="key != 'Observación' && key != 'Observacion' ">{{ value }}</td>
        <td v-if ="key == 'Observación' || key == 'Observacion' " class="text-danger font-weight-bold">{{ value }}</td>
      </tr>
      <b-button v-show="false"
                class="mt-3"
                variant="outline-primary"
                block
                :to="rutaNuevo + '?id=' + idSeleccionado">
            <i class="fa fa-eye"></i> Más información
        </b-button>
    </b-modal>

    <div class="mt-3">
        <h3><i class="fas fa-clipboard-list"></i> Pedidos</h3>
    </div>
    <b-row>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Desde:</label>
            <b-form-datepicker v-model="desde" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Hasta:</label>
            <b-form-datepicker v-model="hasta" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
    </b-row>
    <b-row class="d-flex justify-content-between">
        <div class="col-sm-12 col-md-4 col-lg-2 mb-2">
            <b-button @click="buscarPedidos" block variant="success" class="mr-2">
                <i class="fa fa-search"></i> Buscar
            </b-button>
		</div>
    </b-row>

    <!-- User Interface controls -->
    <b-row class="my-2">
        <b-col md="4">
            <b-form-group label="Filtrar"
                        label-cols-sm="1"
                        label-cols-md="2"
                        label-size="sm"
                        label-for="filterInput">
                <b-input-group size="sm">
                    <b-form-input v-model="filter"
                                type="search"
                                id="filterInput"
                                placeholder="Escribe el filtro..."
                                autocomplete="off">
                    </b-form-input>
                    <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''">
                            <i class="fas fa-times"></i>
                        </b-button>
                    </b-input-group-append>
                </b-input-group>
            </b-form-group>
        </b-col>
        <b-col md="3">
            <b-form-group label="Por página"
                        label-cols-sm="2"
                        label-cols-md="2"
                        label-cols-lg="4"
                        label-size="sm"
                        label-for="perPageSelect">
                <b-form-select v-model="perPage"
                            id="perPageSelect"
                            size="sm"
                            :options="pageOptions">
                </b-form-select>
            </b-form-group>
        </b-col>
        
        <!-- FALTA VERIFICAR COMO TOMAR LOS PERMISOS -->
        <b-col md="3" offset-md="2">
            <b-button @click="redirect"
                    variant="primary"
                    class="btn btn-sm py-1 mt-1 btn-block">
                <i class="fa fa-plus"></i> Nuevo
            </b-button>
        </b-col>
    </b-row>

    <!-- Main table element -->
    <b-container fluid>
            <b-form-group label="Filtrar por planta:">
                <div class="row">
                    <b-form-checkbox-group
                        id="checkbox-group-1"
                        v-model="selectPlantas"
                        :options="plantas"
                        name="flavour-1"
                        @change="seleccionados"
                    >
                    </b-form-checkbox-group>
                    <b-col class="col-md-3 col-sm-12 col-12" align-self="end" v-if="items.length > 0">
                            <b-button @click="exportarExcel" block variant="success" class="btn btn-sm py-1 mt-1 btn-block">
                                <i class="fas fa-file-excel"></i> Exportar Excel
                            </b-button>
                    </b-col>
                </div>
        </b-form-group>
    </b-container>
    <b-table small
            stacked="md"
            head-variant="dark"
            :hover="hover"
            :striped="striped"
            :bordered="bordered"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered">

        <template v-slot:cell(horaSalidaYPlanta)="row">
            <p>{{hora(row.item.horaSalida)}}<br> {{row.item.nombrePlanta}}</p>
        </template>
        <template v-slot:cell(estatusPedido)="row">
            <b-badge variant="success" v-if="row.item.estatus === 'T'" class="p-2">
                Terminado
            </b-badge>
            <b-badge variant="secondary" v-if="row.item.estatus === 'P'" class="p-2">
                Pendiente
            </b-badge>
            <b-badge variant="secondary" v-if="row.item.estatus === 'PV'" class="p-2">
                Pendiente de Confirmar
            </b-badge>
            <b-badge variant="primary" v-if="row.item.estatus === 'PC'" class="p-2">
                Pendiente de Confirmar
            </b-badge>
             <b-badge variant="secondary" v-if="row.item.estatus === 'RG'" class="p-2">
                Reagendar
            </b-badge>
            <b-badge variant="info" v-if="row.item.estatus === 'S'" class="p-2">
                Surtiendo
            </b-badge>
            <b-badge variant="danger" v-if="row.item.estatus === 'C'" class="p-2">
                Cancelado
            </b-badge>
            <b-badge variant="warning" v-if="row.item.estatus === 'DA'" class="p-2">
                Pendiente Autorización
            </b-badge>
            <b-badge variant="warning" v-if="row.item.estatus === 'DC'" class="p-2">
                Pendiente Limite Credito
            </b-badge>
        </template>

        <template v-slot:cell(acciones)="row">

            <b-button v-if="row.item.estatus != 'PC' && row.item.estatus != 'PV'"
                        :disabled=" (row.item.estatus === 'DA' 
										|| row.item.estatus === 'DC'
										|| row.item.estatus === 'C'
                                        || row.item.estatus == 'T')"
                        @click="generarNotaRemision(row.item)"
                        size="sm"
                        class="mr-1"
                        variant="primary"
                        v-b-tooltip.hover
                        title="Generar Nota Remisión">
                <i class="fas fa-tasks"></i>
            </b-button>

            <b-button v-if="row.item.estatus === 'PC' || row.item.estatus === 'PV'"
                        @click="ConfirmarPedido(row.item)"
                        size="sm"
                        class="mr-1"
                        variant="success"
                        v-b-tooltip.hover
                        title="Confirmar Pedido">
                <i class="fas fa-check"></i>
            </b-button>

            <b-button v-if="row.item.estatus === 'PC' || row.item.estatus === 'PV' || row.item.estatus === 'RG'"
                        @click="ModalReagendarPedido(row.item)"
                        size="sm"
                        class="mr-1"
                        variant="secondary"
                        v-b-tooltip.hover
                        title="Reagendar">
                <i class="fas fa-history"></i>
            </b-button>

        </template>

        <template v-slot:cell(cancelarPedido)="row">
            <b-button :disabled="(row.item.estatus == 'S'
									|| row.item.estatus == 'T'
									|| row.item.estatus == 'C')"
                        @click="CancelarPedido(row.item)"
                        size="sm"
                        class="mr-1"
                        variant="danger"
                        v-b-tooltip.hover
                        title="Cancelar Pedido">
                <i class="fa fa-times"></i>
            </b-button>
        </template>

        <template v-slot:cell(actions)="row">
            <b-button @click="modal(row.item, row.index, $event.target)"
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    title="Ver Detalle"
                    variant="outline-info">
                <i class="fa fa-eye"></i>
            </b-button>
            
            <b-button :to="rutaNuevo + row.item.folioPedido"
                        :disabled="(row.item.estatus == 'C')"
                        size="sm"
                        class="mr-1"
                        v-b-tooltip.hover
                        title="Editar"
                        variant="outline-info">
                <i class="fa fa-edit"></i>
            </b-button>
      </template>

    </b-table>
        
    <b-pagination v-model="currentPage"
                    class="mt-1"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="center"
                    aria-controls="my-table">
    </b-pagination>
    
    
</div>

</template>

<script>
import servicioParametro from '@/services/servicio-general';
import servicio from '@/services/servicio-pedidos';
import Swal from 'sweetalert2'
import moment from 'moment'
export default {
    data(){
        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var todayDate = today.toISOString().slice(0,10);

        return{
            refresh: null,
            desde: todayDate,
            hasta: todayDate,
            items: [],
            itemsCopy:[],
            // tempItems:[],
            plantas:[],
            selectPlantas:[],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],


            fields: [
                { key: "folioPedido", label: "Pedido", sortable: true, sortDirection: "desc", },
                { key: "cliente", label: "Cliente", sortable: true, sortDirection: "desc", },
                { key: "vendedor", label: "Vendedor", sortable: true, sortDirection: "desc", },
                // { key: "usuario", label: "Usuario", sortable: true, class: "text-center", },
                { key: "formatoCantidad", label: "Cantidad", sortable: true, class: "text-center" },
                { key: "nomenclatura", label: "Producto", sortable: true, class: "text-center" },
                { key: "fechaSalida", label: "Fecha Salida", sortable: true, class: "text-center" },
                { key: "horaSalidaYPlanta", label: "Hora Salida", sortable: true, class: "text-center" },

                { key: "estatusPedido", label: "Estatus", sortable: true, class: "text-center" },
                { key: "actions", label: "", class: "text-center" },
                { key: "acciones", label: "Acciones", class: "text-center" },
                { key: "cancelarPedido", label: "", class: "text-center" },

            ],
            permisoEscritura: true,
            rutaNuevo: "pedidos/registro/",
            modalShow: false,

            headerBgVariant: "dark",
            headerTextVariant: "light",
            pedidoReagendar: {
				folioPedido: 0,
				fechaSalida: "",
				horaSalida: "",
				motivo: "",
			},
			folioPedidoCancelar: 0,

            infoModal: {
                id: "info-modal",
                title: "",
                content: "",
                codigo: "",
            },
            idSeleccionado: 0,
        }
    },
    components: {
        // TableSortable,
    },
    async mounted() {
        
        await this.ObtenerPedidos();
        await this.CargarRenglones();
        this.refresh = setInterval(() => {
            this.ObtenerPedidos() 
            this.seleccionados()      
        }, 60000);

        this.plantas.push({text:'Planta-1',value:1},{text:'Planta-2',value:2})
        this.itemsCopy = this.items;
    },
    created() {
    },
    destroyed() {
        clearInterval(this.refresh)
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
                        .filter((f) => f.sortable)
                        .map((f) => {
                            return { text: f.label, value: f.key };
                        });
        },
    },
    methods: {
        //Aqui me quede modificación para mostrar solo los campos 
        exportarExcel() {
            this.$loading(true);
            let csv = Object.keys(this.items[0]).join("♪") + "\n"; //headers                   
            for (var item in this.items) {
                csv += Object.values(this.items[item]).join("♪");
                csv += "\n";
            }
            console.log('CSV #1 ', csv);
            
            csv = csv.replaceAll(',', ' ')
            csv = csv.replaceAll('♪', ',')
            
            console.log('CSV #2 ', csv);

            const blob = new Blob([csv], { type: 'text/csv' });
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.setAttribute('hidden', ''); 
            a.setAttribute('href', url);
            a.setAttribute('download', `ListadoPedidos-${this.desde}-${this.hasta}.csv`);
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            this.$loading(false);
        },
        hora(item){
            console.log(item)
            let horas = moment(item, "LT").format('LT')
           
            return horas
        },
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },
        async seleccionados(checked) {
            if (checked.length > 0) {
                this.items = [];
                checked.forEach((chk) => {
                    let tempItems = [];
                    tempItems = this.itemsCopy.filter((x) => x.codPlanta == chk);
                    tempItems.forEach((y) => this.items.push(y));
                });
            } else {
                this.ObtenerPedidos();
            }
            this.CargarRenglones();
        },
        async buscarPedidos() {
            await this.ObtenerPedidos();
            this.itemsCopy = this.items;
        },

        async ObtenerPedidos() {
            let Hora2 = await servicioParametro.obtenerParametro('PrimerAvisoSalida');
            let Hora4 = await servicioParametro.obtenerParametro('SegundoAvisoSalida');
            this.$loading(true);
            
            await servicio.obtenerPedidos(0, this.desde, this.hasta)
                .then((r) => {
                    this.items = r.data;
                    this.CargarRenglones()
                    console.log(r.data);
                        

                    r.data.forEach((X) => {
                        let Salida = moment(`${X.fechaSalida} ${X.horaSalida}`, 'YYYY-MM-DD HH:mm').format('YYYY-MM-DD HH:mm');
                        let Actual4Horas = moment().add(Hora2.data.valor, 'hours').format('YYYY-MM-DD HH:mm');
                        let Actual2Horas = moment().add(Hora4.data.valor, 'hours').format('YYYY-MM-DD HH:mm');
                        
                        if (X.estatus == 'T'){
                            X._rowVariant = "success"
                        }
                        if (X.estatus == 'C'){
                            X._rowVariant = "danger"
                        }
                        if (X.estatus == 'S'){
                            X._rowVariant = "primary"
                        }
                        if (X.estatus == 'PC' || X.estatus == 'PV' || X.estatus == 'RG'){
                             if (moment(Actual4Horas).isBefore(Salida)){
                                servicio.cambiarEstatus(X.folioPedido, 'PC', 'Pendiente de confirmar')
                           }
                            else{
                                if(moment(Salida).isBefore(Actual2Horas)){
                                    servicio.cambiarEstatus(X.folioPedido, 'RG', 'Se acabó el tiempo') 
                                     X._rowVariant = "warning"
                                }
                                else{
                                    servicio.cambiarEstatus(X.folioPedido, 'PV', 'Se acaba el tiempo') 
                               
                                }
                                
                            }
                        }
                       
                   })
                    
                    this.$loading(false);
                })
                .catch((error) => {
                    console.log(error);
                });
        },

        redirect() {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}0` });
            }, 500);
        },

        CargarRenglones() {
            setTimeout(() => {
                this.totalRows = this.items.length;
                console.log(this.totalRows)
            }, 1000);
        },

        async CancelarPedido(item) {
            if (item.estatus == "S" || item.estatus == "T") {
                this.$swal.fire({
					text: "No se puede cancelar el pedido seleccionado.",
					icon: "warning",
                });
            } else {
                let r = await this.$swal.fire({
                // title: "¡ATENCIÓN!",
                text: "¿Desea cancelar el pedido de " + item.cliente + "?",
                icon: "question",
                showCancelButton: true,
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
                });

                if (r.value) {
					this.folioPedidoCancelar = item.folioPedido;
					await this.CapturarObservacion();
                }
            }
        },

        async ConfirmarPedido(item){
            if (item.estatus != "PC" && item.estatus != "PV") {
                this.$swal.fire({
                    text: "No se puede confirmar el pedido, favor de actualizar la pantalla.",
                    icon: "warning",
                });
            } else {

                let r = await this.$swal.fire({
                title: "¡ATENCIÓN!",
                text: "¿Desea confirmar el pedido del cliente: " + item.cliente + ", para la obra: "+ item.obra+", con la cantidad de: "+item.cantidad+"m³?",
                icon: "question",
                showCancelButton: true,
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Aceptar",
                cancelButtonText: "Cancelar",
                });

                if (r.value) {
                    await this.CambiarEstatusPedido(item.folioPedido, true, "ok");
                }
            }
        },

        async ModalReagendarPedido(item){
            this.modalShow = true;
            this.pedidoReagendar.fechaSalida = item.fechaSalida;
            this.pedidoReagendar.horaSalida = item.horaSalida;
			this.pedidoReagendar.folioPedido = item.folioPedido;
			this.pedidoReagendar.motivo = "";
        },

		async ReagendarPedido() {
			if(this.pedidoReagendar.motivo.trim() === "") {
				this.$swal.fire({
                    text: "El campo del motivo, no puede estar vacío.",
                    icon: "warning",
                });
			} else {
                console.log(this.pedidoReagendar)
				await servicio.reagendarPedido(this.pedidoReagendar)
                    .then((r) => {
                        if(r.value) {
                            this.modalShow = false;
                            this.$swal.fire({
                                text: r.message,
                                icon: "success",
                            });
                            this.ObtenerPedidos();
                        }
                        
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
			}

        },

		generarNotaRemision(item) {
			if (item.estatus === "DA" || item.estatus === "DC") {
				this.$swal.fire({
					text: "Favor de contactar al personal de credito y cobranza para autorizar el pedido pedido",
					icon: "warning",
				});
			}
			else if (item.estatus === "C") {
				this.$swal.fire({
					text: "Favor de actualizar la pantalla.",
					icon: "warning",
				});
			} else {
				this.$router.push({
					path: `/dosificador/nota-de-remision/registro/${item.folioPedido}`,
				});
			}
		},

		async CapturarObservacion() {

			let observacion;

			const { value: formValues } = await Swal.fire({
				title: 'Ingrese una observación:',
				html:
					'<textarea id="swal-input2" class="swal2-input" placeholder="Capture una observación" style="margin-top: 20px; margin-bottom: 20px; height: 120px;">',
				focusConfirm: false,
				showCancelButton: true,
				confirmButtonText: 'Aceptar',
				cancelButtonText: 'Cancelar',
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',

				preConfirm: () => {

					observacion = Swal.getPopup().querySelector('#swal-input2').value

					let inputObservacion = Swal.getPopup().querySelector('#swal-input2');

					inputObservacion.style.borderColor = "";

					if (observacion === '') {
						inputObservacion.style.borderColor = "red";
						Swal.showValidationMessage(`Favor de ingresar una observación.`)
					}
				},

			})

			if (formValues) {
				this.observacion = observacion;
				await this.CambiarEstatusPedido(this.folioPedidoCancelar, false, this.observacion);
			}
       
		},

        async CambiarEstatusPedido(folioPedido, confirmado, motivo) {
            this.$loading(true);
            console.log(folioPedido, confirmado, motivo)
            await servicio
                .cambiarEstatusPedido(folioPedido, confirmado, motivo)
                .then((r) => {
					this.folioPedidoCancelar = 0;
					//aqui debe de ir un inicializar
                    this.ObtenerPedidos();
                    if(r.value){
                        this.$swal.fire({
                            title: "¡ÉXITO!",
                            text: r.message,
                            icon: "success",
                        });
                    }else{
                        this.$swal.fire({
                            title: "¡ATENCIÓN!",
                            text: r.message,
                            icon: "info",
                        });
                    }
                    this.$loading(false);
                })
                .catch((error) => {
                    console.log(error);
                });
        },

        modal(item, index, button) {
            let content = "";
            this.idSeleccionado = item.codigo;
            //Checo si la ultimaParteLink viene en el metodo
            this.infoModal.title = `Datos del pedido #${item.folioPedido}`;
                content += `{ `;
                content += `"Folio pedido" : ": ${item.folioPedido}\\n\\n", `;
                content += `"Cliente" : ":  ${item.cliente}\\n\\n",`;
                content += `"Fecha de Salida" : ":  ${item.fechaSalida}\\n\\n",`;
                content += `"Hora de Salida" : ":  ${item.horaSalida}\\n\\n",`;
                content += `"Obra" : ":  ${item.obra}\\n\\n",`;
                content += `"Cantidad" : ":  ${item.cantidad} m³\\n\\n",`;
                content += `"Cierre" : ":  ${item.cierre ? 'Si': 'No'}\\n\\n",`;
                content += `"Cantidad Cierre" : ":  ${item.cantidadCierre} m³\\n\\n",`;
                content += `"Vendedor" : ":  ${item.vendedor}\\n\\n",`;
                content += `"Usuario" : ":  ${item.usuario}\\n\\n",`;
                content += `"Observación" : ":  ${item.observacion}\\n\\n",`;
                content += `"E-1" : ":  ${item.tieneFibra ? ' Fibra ' : 'N/A'} \\n\\n",`
                content += `"E-2" : ":  ${item.tieneImper ? ' Imper ' :'N/A'} \\n\\n"`;
                content += `}`;

                content = JSON.parse(content);
                this.infoModal.content = content;
                this.$root.$emit("bv::show::modal", this.infoModal.id, button);
        },

        resetInfoModal() {
            this.infoModal.title = "";
            this.infoModal.content = "";
        },

    }
}
</script>

